import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import about from '@/components/about'
import test from '@/components/test'
import JobManager from '@/components/job/job-manage'
import WorkRegist from '@/components/work/work-regist'
import WorkManager from '@/components/work/work_manage2'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'test',
      component: test
    },
    {
      path: '/about',
      name: 'about',
      component: about
    },
    {
      path: '/test',
      name: 'test',
      component: test
    },
    {
      path: '/job',
      name: 'job-manage',
      component: JobManager
    },
    {
      path: '/work',
      name: 'WorkManager',
      component: WorkManager
    }
  ]
})



