
import Calendar from 'vux/src/components/calendar/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'


export default {
components: {
Calendar,
Group,
Icon,
XButton,
Flexbox,
FlexboxItem,
InlineXNumber,
XNumber,
Badge,
CellBox
},
data () {
return {
selectAbsenceDate:[],
msg: 'Hello World!',
absenceNum:1,
value:"",
displayFormat (value, type) {
if (type === 'string') {
return value;
} else {
return value.length ? (value.length + ' days') : '';
}
}
}
},
methods:{
onChange(date){
console.log(date);
},
setDisable(date){
var dd = ['2018-11-01','2018-11-18','2018-11-28'];




return false;
},
setStyle(line, index, date){

var dd = ['2018-11-01','2018-11-18','2018-11-28'];


return (dd.indexOf(date.formatedDate) != -1) ? '<div style="height:19px;color:red; font-size:.8rem;">缺勤x3</div>' : '<div style="height:19px;"></div>';

}
}
}



